import React, { useEffect } from 'react'
import { useSelector } from 'react-redux'
import { Helmet } from "react-helmet";
import { GET_LOCAL } from '../../utils';


const HeaderScript = () => {
   const authorized = useSelector(state => state.authorized)
   const emailId = authorized?.userData?.user_data?.user?.emailId ?? "";
   useEffect(() => {
   }, [emailId])
   return <>
      {!GET_LOCAL('isAdmin') && window.location.host.includes('app.') && emailId && !(emailId?.includes('taggbox.com')) ? <Helmet>
         <script>{
               `if(clarity){
                  clarity("set", "userEmail", "${emailId}");
                  clarity("set", "type", "app");
               }`}</script> 
         </Helmet> : null}
   </>
}

export default HeaderScript